body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 12px;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav {
  align-content: space-evenly;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__link {
  color: #000;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  color: #000;
  background-color: #00acdc;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}
